import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

const CentreRegister = () => {
  const [formData, setFormData] = useState({
    centreName: "",
    centreType: "",
    email: "",
    phoneNumber: "",
  });

  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [showOtpModal, setShowOtpModal] = useState(false);
  const [otp, setOtp] = useState("");
  const [otpError, setOtpError] = useState("");
  const [otpMessage, setOtpMessage] = useState("");
  const [otpLoading, setOtpLoading] = useState(false);
  const [registrationSuccess, setRegistrationSuccess] = useState(false);

  // Resend OTP state
  const [resendCooldown, setResendCooldown] = useState(0);

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData({ ...formData, [id]: value });
  };

  // useEffect(() => {
  //   if (message || errorMessage) {
  //     const timer = setTimeout(() => {
  //       setMessage("");
  //       setErrorMessage("");
  //     }, 2000); // clears after 3 seconds
  //     return () => clearTimeout(timer);
  //   }
  // }, [message, errorMessage]);

  useEffect(() => {
    if (resendCooldown > 0) {
      const interval = setInterval(
        () => setResendCooldown((prev) => prev - 1),
        1000
      );
      return () => clearInterval(interval);
    }
  }, [resendCooldown]);

  // Submit registration form â†’ Send OTP
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      const res = await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/send-otp`,
        formData
      );
      if (res.data.success) {
        setMessage(
          `OTP sent to your mobile number: ${formData.phoneNumber}. Please verify to complete registration.`
        );
        setShowOtpModal(true);
        setResendCooldown(Math.ceil(res.data.cooldown/30)); // from backend
      }
    } catch (err) {
      console.error("Error sending OTP:", err);
      setErrorMessage(err?.response?.data?.message || "Something went wrong.");
    } finally {
      setLoading(false);
    }
  };

  // Verify OTP
  const handleVerifyOtp = async () => {
    try {
      setOtpLoading(true);
      const res = await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/verify-otp`,
        { ...formData, otp }
      );

      if (res.data.success) {
        setOtpError("");
        setOtpMessage("");
        setRegistrationSuccess(true);
        // Don't auto-close the modal - user must manually close it
      }
    } catch (err) {
      console.error("OTP verification failed:", err);
      setOtpError(
        err?.response?.data?.message || "Invalid OTP. Please try again."
      );
      setOtpMessage("");
      setOtp("");
    } finally {
      setOtpLoading(false);
    }
  };

  // Handle closing success modal and resetting form
  const handleCloseSuccess = () => {
    setRegistrationSuccess(false);
    setShowOtpModal(false);
    setFormData({
      centreName: "",
      centreType: "",
      email: "",
      phoneNumber: "",
    });
    setOtp("");
    setOtpMessage("");
    setOtpError("");
  };

  // Resend OTP
  const handleResendOtp = async () => {
    try {
      const res = await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/send-otp`,
        formData
      );

      if (res.data.success) {
        setOtpMessage("New OTP sent to your Phone number.");
        setOtpError("");
        setResendCooldown(Math.ceil(res.data.cooldown/30));
      }
    } catch (err) {
      setOtpError(
        err?.response?.data?.message || "Failed to resend OTP. Try again later."
      );
      setOtpMessage("");
    }
  };

   const modalRef = useRef(null);
    
        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              showOtpModal
            ) {
              showOtpModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ðŸ”‘ Scroll lock logic
          if (showOtpModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [showOtpModal]);

  return (
    <div className="dashboard-body__content">
      <div className="card common-card">
        <div className="card-body">
          <div className="row gy-4">
            <div className="col-lg-12 col-12">
              <div className="setting-content">
                {/* Centre Details */}
                <div className="card common-card border border-gray-five overflow-hidden mb-24">
                  <div className="card-header">
                    <h6 className="title">Healthcare Centre Details</h6>
                  </div>
                  <div className="card-body">
                    <form onSubmit={handleSubmit}>
                      <div className="row gy-3">
                        <div className="col-sm-6 col-xs-6">
                          <label htmlFor="centreName" className="form-label">
                           Healthcare Centre Name
                          </label>
                          <input
                            type="text"
                            className="common-input"
                            id="centreName"
                            value={formData.centreName}
                            onChange={handleChange}
                            onKeyDown={(e) => {
                              if (
                                !/^[a-zA-Z\s'-]$|Backspace|Delete|Arrow/.test(
                                  e.key
                                )
                              ) {
                                e.preventDefault();
                              }
                            }}
                            required
                          />
                        </div>
                        <div className="col-sm-6 col-xs-6">
                          <label htmlFor="centreType" className="form-label">
                           Healthcare Centre Type
                          </label>
                          <select
                            className="common-input border"
                            id="centreType"
                            value={formData.centreType}
                            onChange={handleChange}
                            required
                          >
                            <option value="" disabled>
                              Select Type
                            </option>
                            <option value="Hospital">Hospital</option>
                          
                          
                            <option value="General Clinic">
                              General Clinic
                            </option>
                            <option value="Dental Care Centre">
                              Dental Care Centre
                            </option>
                            <option value="Labs and Diagnostic">
                              Labs and Diagnostic
                            </option>
                            <option value="AYUSH Centre">AYUSH Centre</option>
                            <option value="Miscellaneous Healthcare Centre">
                              Miscellaneous Healthcare Centre
                            </option>
                          </select>
                        </div>
                        <div className="col-sm-6 col-xs-6">
                          <label htmlFor="email" className="form-label">
                            Healthcare Centre Email
                          </label>
                          <input
                            type="email"
                            className="common-input"
                            id="email"
                            value={formData.email}
                            onChange={handleChange}
                            required
                          />
                        </div>
                        <div className="col-sm-6 col-xs-6">
                          <label htmlFor="phoneNumber" className="form-label">
                            Healthcare Centre Mobile Number
                          </label>
                          <input
                            type="tel"
                            className="common-input"
                            id="phoneNumber"
                            value={formData.phoneNumber}
                            onChange={handleChange}
                            required
                            onKeyPress={(e) => {
                              const char = e.key;

                              // Only digits allowed
                              if (!/[0-9]/.test(char)) {
                                e.preventDefault();
                                return;
                              }

                              // First digit must be 6,7,8,9
                              if (
                                formData.phoneNumber.length === 0 &&
                                !/[6-9]/.test(char)
                              ) {
                                e.preventDefault();
                              }
                            }}
                            maxLength={10}
                          />
                        </div>
                        {errorMessage && (
                          <div className="alert alert-danger text-center">
                            {errorMessage}
                          </div>
                        )}
                        {/* // : message ? (
                        //   <div className="alert alert-info text-center">
                        //     {message}
                        //   </div>
                        // ) : null} */}

                        <button
                          type="submit"
                          className="btn w-100 btn-main btn-md"
                          disabled={loading}
                        >
                          {loading ? "Sending OTP..." : "Register"}
                        </button>
                      </div>
                    </form>{" "}
                  </div>
                </div>
              </div>
              {/* OTP Modal */}
              {showOtpModal && (
                <div
                  style={{
                    position: "fixed",
                    top: 0,
                    left: 0,
                    right: 0,
                    bottom: 0,
                    backgroundColor: "rgba(0,0,0,0.5)",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    zIndex: 1000,
                  }}
                >
                  <div
                    style={{
                      backgroundColor: "#fff",
                      borderRadius: "10px",
                      maxWidth: "500px",
                      width: "90%",
                      boxShadow: "0 0 10px rgba(0,0,0,0.3)",
                      overflowY: "auto",
                      paddingBottom: "20px",
                      position: "relative",
                    }}
                  >
                    {!registrationSuccess ? (
                      // OTP Verification State
                      <>
                        {/* Header */}
                        <div
                          style={{
                            backgroundColor: "#3a3a3a",
                            color: "#fff",
                            padding: "15px 20px",
                            fontSize: "18px",
                            fontWeight: "bold",
                            position: "relative",
                          }}
                        >
                          <i className="fas fa-key me-2"></i> Verify OTP
                          <button
                            onClick={() => setShowOtpModal(false)}
                            style={{
                              position: "absolute",
                              top: "10px",
                              right: "10px",
                              background: "transparent",
                              border: "none",
                              fontSize: "22px",
                              color: "#fff",
                              cursor: "pointer",
                            }}
                          >
                            &times;
                          </button>
                        </div>

                        {/* Body */}
                        <div style={{ padding: "20px", color: "#333" }}>
                          <p>
                            An OTP has been sent to your mobile number:{" "}
                            <strong>{formData.phoneNumber}</strong>
                          </p>

                          <input
                            type="text"
                            className="form-control"
                            placeholder="Enter OTP"
                            value={otp}
                            onChange={(e) => setOtp(e.target.value)}
                            maxLength="6"
                            style={{
                              marginTop: "10px",
                              textAlign: "center",
                              fontSize: "18px",
                              letterSpacing: "5px",
                            }}
                          />

                          {/* Alerts */}
                          {otpError && (
                            <div
                              style={{
                                backgroundColor: "#fdecea",
                                color: "#d93025",
                                padding: "10px",
                                borderRadius: "6px",
                                marginTop: "10px",
                              }}
                            >
                              {otpError}
                            </div>
                          )}

                          {otpMessage && (
                            <div
                              style={{
                                backgroundColor: "#e7f7ed",
                                color: "#0f9d58",
                                padding: "10px",
                                borderRadius: "6px",
                                marginTop: "10px",
                                display: "flex",
                                alignItems: "center",
                              }}
                            >
                              <i className="fas fa-check-circle me-2"></i>
                              <strong>{otpMessage}</strong>
                            </div>
                          )}
                        </div>

                        {/* Footer */}
                        <div
                          style={{
                            padding: "0 20px 20px 20px",
                            display: "flex",
                            flexDirection: "column",
                            gap: "10px",
                          }}
                        >
                          <div style={{ textAlign: "right" }}>
                            {resendCooldown > 0 ? (
                              <span style={{ color: "gray", fontSize: "14px" }}>
                                Resend in {Math.ceil(resendCooldown/60)}s
                              </span>
                            ) : (
                              <button
                                onClick={handleResendOtp}
                                style={{
                                  color: "#007bff",
                                  background: "none",
                                  border: "none",
                                  cursor: "pointer",
                                  fontSize: "14px",
                                }}
                              >
                                Resend OTP
                              </button>
                            )}
                          </div>

                          <button
                            onClick={handleVerifyOtp}
                            disabled={otpLoading}
                            style={{
                              backgroundColor: "#007bff",
                              color: "#fff",
                              border: "none",
                              borderRadius: "6px",
                              padding: "10px",
                              fontSize: "16px",
                              cursor: "pointer",
                            }}
                          >
                            {otpLoading ? "Verifying..." : "Verify OTP"}
                          </button>
                        </div>
                      </>
                    ) : (
                      // Registration Success State
                      <>
                        {/* Header */}
                        <div
                          style={{
                            backgroundColor: "#28a745",
                            color: "#fff",
                            padding: "15px 20px",
                            fontSize: "18px",
                            fontWeight: "bold",
                            position: "relative",
                          }}
                        >
                          <i className="fas fa-check-circle me-2"></i>{" "}
                          Registration Successful
                        </div>

                        {/* Body */}
                        <div
                          style={{
                            padding: "20px",
                            textAlign: "center",
                            color: "#333",
                          }}
                        >
                          <i
                            className="fas fa-check-circle text-success"
                            style={{ fontSize: "4rem", color: "#28a745" }}
                          ></i>
                          <h5 className="mt-3">OTP Verified Successfully!</h5>
                          <p className="mt-2">
                            Please check your email to complete the registration
                            process.
                          </p>
                          <div
                            style={{
                              backgroundColor: "#e7f3ff",
                              padding: "15px",
                              borderRadius: "8px",
                              textAlign: "left",
                              marginTop: "15px",
                            }}
                          >
                            <i
                              className="fas fa-envelope text-primary me-2"
                              style={{ fontSize: "1.2rem" }}
                            ></i>
                            <strong>Email sent to:</strong>
                            <p style={{ color: "#007bff", margin: "5px 0" }}>
                              {formData.email}
                            </p>
                            <small style={{ color: "#666" }}>
                              Follow the instructions in the email to set your
                              password.
                            </small>
                          </div>
                          <div
                            className="alert alert-warning border-0 mt-3"
                            style={{ backgroundColor: "#fff8e1" }}
                          >
                            <small className="text-muted">
                              <i className="fas fa-info-circle me-1"></i>
                              Don't forget to check your spam/junk folder if you
                              don't see the email in your inbox.
                            </small>
                          </div>
                        </div>

                        {/* Footer */}
                        <div style={{ padding: "0 20px 20px 20px" }}>
                          <button
                            onClick={handleCloseSuccess}
                            style={{
                              backgroundColor: "#007bff",
                              color: "#fff",
                              border: "none",
                              borderRadius: "6px",
                              padding: "10px",
                              width: "100%",
                              fontSize: "16px",
                              cursor: "pointer",
                            }}
                          >
                            <i className="fas fa-times me-2"></i> Close
                          </button>
                        </div>
                      </>
                    )}
                  </div>
                </div>
              )}
              <div className="col-sm-12 mb-0 mt-5">
                <div className="have-account">
                  <p className="text font-14">
                    Already Registered?{" "}
                    <Link
                      className="link text-main text-decoration-underline fw-500"
                      to="/centre-login"
                    >
                      Login
                    </Link>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CentreRegister;
